/* GoToTopButton.css */
.go-to-top-button {
    position: fixed;
    bottom: 30px;
    right: 20px;
    display: none;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
    z-index: 50;
  }
  
  .go-to-top-button.show {
    display: block;
  }
  